@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@500&display=swap);
.HelpPage {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}

.HelpPage .ContentArea {
    margin: 25px 0;
}

.HelpPage .downloadLatestVersion {
    display: flex;
}

@media (max-width: 1000px) {
    .HelpPage .downloadLatestVersion .ImageLabel {
        width: 150px !important;
        height: 150px !important;
    }
}

@media (max-width: 600px) {
    .HelpPage .downloadLatestVersion .ImageLabel {
        display: none;
    }
    .HelpPage .downloadLatestVersion .infoArea {
        margin-left: unset !important;
    }
}

.HelpPage .installation .downloadButton {
    margin: 10px 0;
    padding: 10px 15px;
    width: 100%;
    font-size: 15px;
    text-align: center;
    color: white;
    background-color: #00cf00;
    text-decoration: none;
}

.HelpPage .installation .downloadButton:hover {
    background-color: #00b400;
}

.HelpPage .installation .downloadButton:active {
    background-color: #008600;
}

.HelpPage .imageToggler {
    position: fixed;
    display: flex;
    bottom: 30px;
    right: 30px;
    padding: 10px;
    background-color: rgba(145, 71, 255, 0.7);
    border-radius: 100px;
}
.PageFrame {
    margin: 0 auto;
    padding: 10px;
    width: 100%;
    max-width: 1200px;
}
.ContentArea {
    padding: 20px 10px;
    background-color: #ffffff;
    border: 1px solid #e7eaec;
    border-top: 3px solid #e5e5e5;
    border-radius: 5px;
}
.ContentBox {
    margin: 10px;
    padding: 10px;
    background-color: #f3f4f5;
    border: 1px solid #e0e2e4;
    border-radius: 5px;
    font-family: 'Nanum Gothic', sans-serif;
}
.ContentSubtitle {
    margin-bottom: 20px;
    padding: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-bottom: 2px solid #000000;
    font-size: 16pt;
    font-family: 'Nanum Gothic', sans-serif;
}
.ContentTextArea {
    padding: 5px 10px;
    font-size: 12pt;
}

.ContentTextArea p {
    margin-bottom: 16px;
}
.ContentTitleBar {
    margin: 30px 0;
    padding: 30px;
    background-color: #ffffff;
    border: 1px solid #e7eaec;
    border-top: 3px solid #eea3aa;
    border-radius: 5px;
    font-size: 15pt;
}
.ContentSrcViewer {
    margin: 10px;
    padding: 15px;
    color: #f8f8f2;
    background-color: #272822;
    font-family: 'Source Code Pro', monospace;
    word-break: break-all;
}
.HashTag {
    display: inline-block;
    margin: 2px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0 5px;
    font-size: 17px;
    background-color: rgba(150, 150, 150, 0.5);
    border-radius: 5px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

.HashTag:hover {
    background-color: rgba(150, 150, 150, 0.8);
}
.ImageViewer {
    padding: 15px;
    max-width: 100%;
    max-height: 100%;
}
.Switch {
    margin: auto;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
}

.Switch .switch-checkbox {
    display: none;
}

.Switch .switch-label {
    display: block;
    position: relative;
    margin: unset;
    width: 50px;
    height: 26px;
    border-radius: 50px;
    background-color: #888888;
}

.Switch .label-switched {
    background-color: #00cf00;
}

.Switch .switch-ball {
    position: absolute;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    top: 2px;
    left: 2px;
    transition: transform 0.2s linear;
    background-color: #ffffff;
}

.Switch .ball-switched {
    transform: translateX(24px);
}
